body,
p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

body {
    font-family: 'Proxima Nova', Georgia, sans-serif;
    background: #000;
    color: #fff;
}

.header {
  position: fixed;
  width: 940px;
  z-index: 1000;
}

.footer {
  position: fixed;
  width: 100%;
  bottom: 0;
  background: #222;
  height: 80px;
}

.app-container {
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.left-side-section {
  position: fixed;
  top: 10px;
  left: 10px;
  bottom: 10px;
  width: 180px;
  overflow-y: auto;
}

.main-section {
  width: 940px;
  position: relative;
  left: 200px;
  padding: 0 10px;
}



